*{
    margin: 0px;
    margin-left: 0px;
    margin-right: 0px;
    padding: 0px;
    
}
.navbar{
    background-image: linear-gradient(rgb(33, 27, 27, 0.9), rgb(233, 233, 227, 0.0));
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    height: 50px;
    width: 100%;
    z-index: 5;
    
}
.rightleft{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.middlenav{
    margin-top: auto;
}
.leftnav{
    display: flex;
    align-items: center;
}
.leftnav img{
    height: 55px;
    margin-top: 39px;
    margin-left: 9px;
}
.rightnav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 15px;
}

.search{
    display: flex; 
    align-items: center;
    margin-top: 2px;
    border: 1px solid rgb(149, 111, 111);
    border-radius: 4px;
    
}
.search input{
    height: 25px;
    border: none;
    padding-left: 4px;
    background: rgb(255, 255, 255,0.3);
    outline: none;
    
}
.search_icon{
   background: rgb(255, 255, 255, 0.3); 
}
.cart_btn{
    margin-right: 20px;
}
.leftnav h2{
  color: rgb(242, 173, 113);  
  text-shadow: 2px 2px 2px rgb(53, 37, 37); 
}
.signin{
    margin-right: 20px;
}
.extrasearch{
    position: absolute !important;
    background-color: rgb(248, 246, 246, 0.6);
    color: rgb(10, 10, 10);
    top: 50px;
    right: 30%;
    width: 440px;
    border-radius: 7px;
    opacity: 95%;
    z-index: 2;
}
.extrasearch a{
    text-decoration: none;
    color: rgb(12, 1, 1);
    background-color: rgb(248, 246, 246, 0.1);
}

.avtar{
    background-color: rgb(95, 86, 225) !important;
    /* margin-right: 15px; */
    cursor: pointer;
    box-shadow: 1px 2px 2px black;
}
#sign{
    color: rgb(7, 167, 7);
    text-decoration: none;
    font-weight: bold;
    text-shadow: 1px 1px 1px black;
}
.cart_btn{
   color: rgb(11, 11, 11);
    text-shadow: 2px 2px 2px black;
    box-shadow: 2px 2px 2px black;
}

 .extrasearch a:hover{
font-weight: bold;
}

@media screen and (max-width:500px){
    .avtar{
display: none !important;
        
    }
    .signin{
        display: none;
    }
    .hamburger{
        position: absolute;
        right: 22px;
    
    }
    .search input{
        width: 100px;
    }

   
}
.menu1{
    width:30px !important;
    height:30px !important;
    color: #181717;
   
}
@media screen and (max-width:800px){
    .extrasearch{
        position: absolute !important;
        background-color: rgb(248, 246, 246, 0.6);
        color: rgb(10, 10, 10);
        top: 50px;
        right: 30%;
        left: 5%;
        width: 440px;
        border-radius: 7px;
        opacity: 95%;
        z-index: 2;
    }
}

